html, body {
	margin: 0;
	padding: 0;
	height: 100%;
	min-height: 100%;
}

body {
	position: relative;
}

.k-presentation {
	position: relative;
	margin: 0 1em 1em;
	background: #ccc;
	width: 200px;
	height: 150px;

	&.isShown {
		position: absolute;
		top: 50%;
		left: 50%;
		overflow: hidden;
		margin: -320px 0 0 -512px;
		width: 1024px;
		height: 640px;
		background: #000;

		.frame {
			width: 1024px;
			height: 640px;
		}
	}
}

.frame {
	display: none;
	width: 100%;
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background: #eee;

	&:first-child, &.active {
		display: block;
	}
}